* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

p > a {
  margin-left: 1px;
  margin-bottom: 15px;
  height: 20px;
  width: 60px;
  border-radius: 30px;
  background-color: rgb(146, 145, 146);
  color: #fff;
  display: grid;
  align-items: center;
  text-align: center;
}
